<template>
  <div class="container">
    <div class="eosdeposit">
      <el-form :model="depositForm" ref="depositForm" label-width="60px" :rules="depositRules">
        <el-form-item :label="this.$t('assetScene.amount')" prop="amount">
          <div>
            <el-input
              :placeholder="this.$t('assetScene.depositamount')"
              v-model="depositForm.amount"
              class="input-with-select"
            >
              <el-select
                popper-class="input-with-select-coin"
                v-model="depositForm.coin_code"
                slot="append"
                placeholder="请选择"
                style="width:120px"
              >
                <el-option label="EOS" value="EOS"></el-option>
                <el-option label="ONES" value="ONES"></el-option>
              </el-select>
            </el-input>
          </div>
        </el-form-item>
        <!-- <el-form-item :label="$t('loginPart.VerificationCode')" prop="vcode">
          <el-input v-model="depositForm.vcode">
            <el-button
              type="primary"
              plain
              @click="sendCode"
              :loading="smsloading"
              :disabled="smsdisabled"
              slot="append"
              style="width:120px"
            >{{VerificationCode}}</el-button>
          </el-input>
        </el-form-item>-->
        <el-form-item label-width="0px" align="center">
          <el-button
            type="primary"
            :loading="loading"
            @click="deposit"
            style="width:50%;"
          >{{this.$t("assetScene.deposit")}}</el-button>
        </el-form-item>
      </el-form>

      <div class="current">
        {{this.$t("assetScene.AccountBalance")}}
        <span>{{cur_coin_amount}} {{cur_coin_code}}</span>
      </div>

      <!-- <div class="erc20deposit-tip">
        <p>{{this.$t("assetScene.depositTips9")}}</p>
      </div>-->
    </div>
  </div>
</template>

<script>
import AssetService from '../../../api/asset'
import Util from '../../../utils/util'
import EosService from '../../../utils/eosservice'

export default {
  data() {
    var validateAmount = (rule, value, callback) => {
      if (value == '')
        return callback(new Error(this.$t('assetScene.depositPlaceholder')))
      return callback()
    }
    var validateVcode = (rule, value, callback) => {
      if (!this.depositForm.order_id) {
        callback(new Error(this.$t('loginPart.VerificationCodeFormatError3')))
      }
      if (value === '') {
        callback(new Error(this.$t('loginPart.VerificationCodePlaceHolder')))
      } else if (!/\d/.test(value)) {
        callback(new Error(this.$t('loginPart.VerificationCodeFormatError1')))
      } else if (value.length != 6) {
        callback(new Error(this.$t('loginPart.VerificationCodeFormatError2')))
      } else {
        callback()
      }
    }
    return {
      eosio: null,

      loading: false,
      cur_coin_code: 'EOS',
      cur_coin_amount: '0.00',

      VerificationCode: this.$t('loginPart.sendCode'),
      smsloading: false,
      smsdisabled: false,

      depositForm: {
        order_id: '',
        amount: '',
        coin_code: 'USDT',
        vcode: ''
      },
      balances: {
        usdt: 0,
        dt: 0,
        ones: 0,
        just: 0
      },
      depositRules: {
        amount: [{ validator: validateAmount, trigger: 'blur' }],
        vcode: [{ validator: validateVcode, trigger: 'blur' }]
      }
    }
  },
  watch: {
    'depositForm.coin_code': function(val) {
      console.log(val)
      var key = val.toLowerCase()
      this.cur_coin_code = val.toUpperCase()
      this.cur_coin_amount = this.balances[key]
    }
  },

  mounted: function() {
    this.eosio = this.$store.getters.eosio
    //action=deposit
    var coin_code = this.$route.params.coin_code
    var coin_amount = this.$route.params.coin_amount
    if (coin_code) {
      this.cur_coin_code = coin_code.toUpperCase()
      this.depositForm.coin_code = coin_code.toUpperCase()
      this.cur_coin_amount = Number(coin_amount).toFixed(4)
    }
    const action = this.$route.query.action

    this.getBalances()
  },

  watch: {
    loading(val) {
      if (!val && this.loadingPage) {
        this.loadingPage.close()
      }
    }
  },
  methods: {
    deposit() {
      var that = this
      this.$refs.depositForm.validate(valid => {
        if (valid) {
          this.loading = true
          var amount = this.depositForm.amount
          var coin_code = this.depositForm.coin_code.toLowerCase()
          var params = {
            type: 'eos',
            amount: amount,
            coin_code: coin_code
          }
          console.log(params, this.eosio)
          if (!this.eosio) {
            this.eosio = new EosService(process.env.EOS_CONTRACT_NAME)
            this.$store.dispatch('ASET_EOSIO', this.eosio)

            // this.eosio = this.$store.getters.eosio
            console.log('eos connecting')
            this.eosio.connect(
              function(code, msg, data) {
                console.log(code, msg, data)
                if (code == 0) {
                  this.predeposit(params)
                } else {
                  this.loading = false

                  this.$message({
                    message: this.$t('message.' + msg), //todo
                    type: 'error',
                    center: true
                  })
                }
              }.bind(this)
            )
          } else {
            console.log('eos connectd', this.eosio)

            this.predeposit(params)
          }
        } else {
          return false
        }
      })
    },

    predeposit(params) {
      AssetService.predeposit(params).then(res => {
        if (res.code == 0) {
          this.loadingPage = this.$loading({
            lock: true,
            text: this.$t('message.paying'),
            spinner: 'el-icon-loading',
            background: 'rgba(0, 0, 0, 0.7)'
          })

          var memo = 'deposit#' + res.data.eos_orderid
          this.eosio.transfer(
            params.coin_code,
            res.data.amount,
            memo,
            function(code, msg, data) {
              console.log(code, msg, data)
              if (code == 0) {
                this.dodeposit(res.data.order_id, data.txid, 0)
              } else {
                this.loading = false
                this.$message({
                  message:
                    msg == 'tx_cpu_usage_exceeded'
                      ? this.$t('message.' + msg)
                      : msg,
                  type: 'error',
                  center: true
                })
              }
            }.bind(this)
          )
        } else {
          this.loading = false

          this.$message({
            message: this.$t('message.' + res.msg),
            type: 'error',
            center: true
          })
        }
      })
    },

    dodeposit(order_id, txid, counter) {
      var params = {
        type: 'eos',
        order_id: order_id,
        txid: txid
      }

      AssetService.deposit(params).then(res => {
        console.log('deposit', params, res)
        if (res.code == 0) {
          if (res.data.status == 0) {
            this.loading = false
            this.$message({
              message: this.$t('message.deposit_success'),
              type: 'success',
              center: true
            })

            this.depositForm.order_id = ''
            this.depositForm.amount = ''
            this.getBalances()
          } else {
            if (counter < 10) {
              setTimeout(
                function() {
                  this.dodeposit(order_id, txid, ++counter)
                }.bind(this),
                1000
              )
            } else {
              this.$message({
                message: this.$t('message.eos_waiting'),
                type: 'error',
                center: true
              })
              this.loading = false
            }
          }
        } else {
          this.$message({
            message: this.$t('message.' + res.msg),
            type: 'error',
            center: true
          })
          this.loading = false
        }
      })
    },
    getBalances() {
      AssetService.balances().then(res => {
        if (res.code == 0) {
          for (var i in res.data.coins) {
            this.balances[i] = res.data.coins[i]['balances']['eos']
              ? res.data.coins[i]['balances']['eos']
              : '0.00'
          }
          // this.balances = res.data.coins

          console.log(this.cur_coin_code)
          var key = this.cur_coin_code.toLowerCase()
          console.log(this.balances, key)
          this.cur_coin_amount = Number(this.balances[key]).toFixed(4)
        }
      })
    }
  }
}
</script>

<style scoped>
.eosdeposit {
  width: 96%;
  margin: 0 auto;
}

.container {
  width: 100%;
  /* height: 100vh; */
  margin-top: 45px;
  box-sizing: border-box;
  overflow: hidden;
}

.current {
  width: 100%;
  line-height: 7vh;
  margin-top: 0vh;
  font-size: 0.9rem;
  color: #888;
  text-align: right;
}
.current span {
  color: #ffc02d;
  font-size: 0.9rem;
  font-weight: 800;
}

.erc20deposit-tip {
  padding: 10px 15px;
  line-height: 22px;
  font-size: 12px;
  color: #e64c65;
}

.input-with-select .el-input-group__append {
  background-color: #fff;
}
</style>


